.work {
  // height: 630px;
  background-color: #001F1F;
  padding-top: 155px;
  padding-bottom: 133px;
  color: var(--light-color);
  @include small-tablet {
    padding: var(--offset) 0;
  }

  &__left {
    grid-column: 4 span;
    padding-right: 50px;
    @include small-tablet {
      padding-right: 0;
    }
  }

  &__title {
    color: inherit;
    margin: 0;
    margin-bottom: 43px;
    font-family: var(--second-family);
    font-size: 36px;
    font-style: normal;
    font-weight: 400;
    line-height: 52px;
    letter-spacing: 1.2px;
  }

  &__descr {
    margin: 0;
    margin-bottom: 37px;
    color: inherit;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 28px;
  }

  &__btn {
    color: inherit;
    text-align: center;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0.02px;
    background: #00665F;
    border: none;
    &:focus {
      outline: none;
    }
    @include hover {
      border-color: var(--accent-color);
      background-color: var(--accent-color);
    }
    &:active {
      border-color: var(--active-color);
      background-color: var(--active-color);
    }
  }
  work__btn.focus-visible {
    border-color: var(--active-color);
  }

  &__right {
    grid-column: 7 / 5 span ;
    @include small-tablet {
      grid-column: 4 span;
      margin-top: var(--offset);
    }
  }

}
