.header {
  padding: 17px 0;
  &__container {
    justify-content: space-between;
    align-items: center;
  }
  &--position {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 10;
  }
}

.nav {
  padding-right: 16px;
  align-items: center;
  &__list {
    margin-right: 32px;
    gap: 51px;
    @include small-tablet {
      margin: 0;
      flex-direction: column;
      align-items: flex-start;
    }
  }
  &__link {
    font-size: var(--main-size);
    color: var(--light-color);
    transition: color .3s ease-in-out;
    &:focus {
      outline: none;
    }
    @include hover {
      color: (var(--accent-color));
    }
    &:active {
      color: var(--active-color);
    }
    @include small-tablet {
      font-size: 16px;
    }
  }
  .nav__link.focus-visible {
    outline: 1px solid var(--active-color);
  }
  @include small-tablet {
    position: fixed;
    padding: var(--offset);
    left: 0;
    top: 0;
    width: 60%;
    height: 100vh;
    background-color: #001414 ;
    // display: none;
    gap: 51px ;
    flex-direction: column;
    align-items: flex-start;
    overflow-y: auto;
    visibility: hidden;
    transform: translateX(-100%);
    transition: transform 0.3s ease-in-out, visibility 0.3s ease-in-out;
  }
}

.nav.menu--active {
  visibility: visible;
  transform: none;
}
