// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: 'PublicSans', sans-serif;
  --second-family: 'LibreBaskerville', serif;
  --content-width: 1152px ;
  --container-offset: 15px ;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --offset: 32px ;


  // sizes
  --second-size: 15px ;
  --main-size: 13px ;
  // colors
  --light-color: #fff;
  --accent-color: #00A1FF;
  --active-color: #E00E00;
  --round-color: #014C48 ;
  --dark-color: #003331 ;
}
