.approach {
  &__top {
    padding: 80px 0;
    background-color: #F7F8F8;
    @include small-tablet {
      padding: 60px 0;
    }
  }

  &__bottom {
    background-color: var(--light-color);
  }

  &__list {
    @include tablet {
      row-gap: var(--offset);
    }
  }

  &__left {
    padding-top: 33px;
    grid-column: 5 span;
    @include big-desktop {
      margin-bottom: var(--offset);
    }
    @include small-tablet {
      grid-column: 4 span;
    }
  }
  &__right {
    display: flex;
    align-items: flex-start;
    justify-content: flex-end;
    grid-column: 7 / 6 span;
    gap: 8px;
    @include big-desktop {
      grid-column: 12 span;
      justify-content: flex-start;
    }
    @include small-tablet {
      grid-column: 4 span;
      flex-wrap: wrap;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 13px;
    color: var(--dark-color);
    font-size: 22px;
    line-height: 22px;
    font-weight: 400;
  }

  &__descr {
    margin: 0;
    margin-bottom: 37px;
    color: rgba(53, 83, 98, 0.80);
    font-size: var(--second-size);
    line-height: 24px;
  }


  &__btn {
    border: 1px solid rgba(53, 83, 98, 0.2);
    padding: 16px 34px;
    color: #00413e;
  }
  &__bottom {
    padding: 24px 0;
  }


  &__item {
    grid-column: span 2;
  }

  &__link {
    display: block;
    padding-top: 17px;
    padding-left: 32px;
    border-top: 1px solid rgba(53, 83, 98, 0.30);
    font-size: var(--second-size);
    line-height: 24px;
    color: rgba(53, 83, 98, 0.80);
    font-size: var(--second-size);
    font-style: normal;
    font-weight: 400;
    line-height: 24px;
    color: inherit;
    background-size: 24px 24px ;
    background-repeat: no-repeat;
    transition: color 0.3s ease-in-out, border-color 0.3s ease-in-out;
    background-position: left top 15px;

    &:focus {
      outline: none;
    }

    @include hover {
      color: (var(--accent-color));
      border-color: (var(--accent-color));
    }

    &:active {
      color: var(--active-color);
      border-color: (var(--active-color));
    }

    &--ted {
      background-image: url('../img/svg/IMAGE.svg')  ;
    }

    &--nyt {
      background-image: url('../img/svg/IMAGE (1).svg')  ;
    }

    &--scoop {
      background-image: url('../img/svg/IMAGE (2).svg')  ;
    }

    &--press {
      background-image: url('../img/svg/IMAGE (3).svg')  ;
    }

    &--time {
      background-image: url('../img/svg/IMAGE (4).svg')  ;
      }
      &--freethink {
        background-image: url('../img/svg/IMAGE (5).svg')  ;
      }
    }
    &__link.focus-visible {
      outline: 1px solid var(--active-color);
    }
  }
