.partners {
  padding: 16px 0 ;
  &__container {
    column-gap: 6px;
    align-items: center;
    @include big-desktop {
      justify-content: space-between;
    }
    @include tablet {
      flex-wrap: wrap;
      justify-content: flex-start;
      gap: 10px;
    }
  }
  &__list {
    display: flex;
    align-items: center;
    column-gap: 12px;
    @include tablet {
      flex-wrap: wrap;
      justify-content: flex-start;
    }
  }
  &__link {
    color: rgba(53, 83, 98, 0.60);
    font-size: 11px;
    font-weight: 400;
    line-height: 16px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    transition: color 0.3s ease-in-out;
    &:focus {
      outline: none;
    }


    @include hover {
      color: (var(--accent-color));
      border-color: (var(--accent-color));
    }

    &:active {
      color: var(--active-color);
      border-color: (var(--active-color));
    }
  }
  &__link.focus-visible {
    outline: 1px solid var(--active-color);
  }
  &__logo {
    @include big-desktop {
      width: 140px;
    }
  }
}
