.information {
  position: relative;
  background-color:#F4F5F5;
  padding-bottom: 140px;

  &__left {
    padding-right: 136px;
    grid-column: 6 span;
    @include small-tablet {
      grid-column: 4 span;
      padding-right: 0;
    }
  }

  &__right {
    grid-column: 7/6 span;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    column-gap: var(--offset);
    row-gap: 36px;
    @include small-tablet {
      grid-template-columns: repeat(4, 1fr);
      grid-column: 4 span;
    }
  }
  &__item {
    grid-column: 3 span;
    @include small-tablet {
      grid-column: 4 span;
    }
  }

  &__title {
    margin: 0;
    color: var(--dark-color);
    font-family: var(--second-family);
    font-size: 31px;
    font-weight: 400;
    line-height: 38px;
    letter-spacing: 1.2px;
    @include small-tablet {
      margin-bottom: 20px;
    }
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 4px;
    color: var(--dark-color);
    font-size: var(--second-size);
    font-style: normal;
    font-weight: 400;
    line-height: 24px;
  }

  &__descr {
    margin: 0;
    color: rgba(53, 83, 98, 0.80);
    font-size: var(--second-size);
    font-style: normal;
    font-weight: 400;
    line-height: 24px;
  }
  @include small-tablet {
    grid-column: 4 span;
    padding-bottom: 0;
    padding: var(--offset) 0;
  }
}
