.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-size: var(--main-size);
  line-height: 20px;
  letter-spacing: 0.02px;
  border-radius: 80px;
  border: 1px solid rgba(255, 255, 255, 0.30);
  text-align: center;
  padding: 17px 33px;
  color: var(--light-color);
  cursor: pointer;
  // ЧТОБЫ БРАУЗЕР ЧЕТКО ПОНИМАЛ ЧТО МЫ ДОЛЖНЫ ПЛАВНО МЕНЯТЬ ТОЛЬКО ЦВЕТ ФОНА И ГРАНИЦЫ
  transition: background-color 0.3s ease-in-out, border-color 0.3s ease-in-out;

  //ДОБАВЛЯЕМ МИКСИН ОТКЛЮЧАЕМ HOVER НА МОБИЛКАХ И ДЕЛАЕМ ТОЛЬКО НА DESCTOP
  &:focus {
    outline: none;
  }

  // МЕНЯЕТСЯ ЦВЕТ ПРИ НАВЕДЕНИИ
  @include hover {
    border-color: var(--accent-color);
    background-color: var(--accent-color);
  }

  // В АКТИВНОМ СОСТОЯНИИ МЕНЯЕТСЯ НА КРАСНЫЙ ЦВЕТ
  &:active {
    border-color: var(--active-color);
    background-color: var(--active-color);
  }


  // ФОКУС БУДЕТ РАБОТАТЬ ТОЛЬКО С КЛАВИАТУРЫ, А НЕ С МЫШКИ
}

// ДЛЯ ВЫДЕЛЕНИЯ ГРАНИЦ
.btn.focus-visible {
  border-color: var(--active-color);
}
