.footer {
  background: #001414;
  &__top {
    padding: 112px 0 137px 0;
    @include small-tablet {
      padding: var(--offset) 0;
    }
  }
  &__item {
    &-1 {
      grid-column: 1 span;
    }
    &-2 {
      grid-column: 3 / 2  span ;
    }
    &-3 {
      grid-column: 5 / 2 span ;
      @include small-tablet {
        grid-column: 2 span;
      }
    }
    &-4 {
      grid-column: 11 / 1 span;
      // @include small-tablet {
      //   grid-column: 4 span;
      // }
    }
    @include small-tablet {
      grid-column: 2 span;
      row-gap: var(--offset);
    }
  }
  &__list {
    @include small-tablet {
      row-gap: var(--offset);
    }
  }
  &__title {
    position: relative;
    margin: 0;
    margin-bottom: 48px;
    color: #7D9798;
    font-size: var(--main-size);
    font-style: normal;
    font-weight: 400;
    line-height: 16px;
    letter-spacing: 1px;
    text-transform: uppercase;
    transition: 0.3s ease-in-out all;
    &::before {
      position: absolute;
      content: '';
      background-color: #25B895;
      left: 0;
      bottom: -24px;
      height: 1px;
      width: 16px;
    }
  }
  &__link {
    color: #EDECEB;
    font-size: var(--second-size);
    font-style: normal;
    font-weight: 400;
    line-height: 30px;
    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--accent-color);
      border-color: var(--accent-color);
    }

    &:active {
      color: var(--active-color);
      border-color: var(--active-color);
    }
}
.footer__link.focus-visible {
  outline: 1px solid var(--active-color);
}
  &__bottom {
    height: 136px;
    padding: 40px 64px 60px 64px ;
    justify-content: space-between;
    align-items: center;
    @include small-tablet {
      flex-direction: column;
      row-gap: var(--offset);
      padding: var(--offset) 0;
    }
  }
  &__wrapper {
    background: #000F0F;
  }
  &__authors {
    width: 264px;
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
    color: #E5ECEC;
  }
  &__text {
    color: inherit;
    font-size: 10px;
    font-style: normal;
    font-weight: 400;
    line-height: 10px;
    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--accent-color);
      border-color: var(--accent-color);
    }

    &:active {
      color: var(--active-color);
      border-color: var(--active-color);
    }
    }
  .footer__text.focus-visible {
    outline: 1px solid var(--active-color);
  }
    &__pl {
      padding-left: 28px;
    }
}
