.steps {
  padding: 120px 0;
  background-color: var(--light-color);
  @include small-tablet {
    padding: 60px 0;
  }
  &__title {
    margin: 0 auto;
    margin-bottom: 57px;
    max-width: 720px;
    color: var(--dark-color);
    text-align: center;
    font-family: var(--second-family);
    font-size: 30px;
    font-style: normal;
    font-weight: 400;
    line-height: 38px;
    letter-spacing: 1.2px;
    @include small-tablet {
      font-size: 24px;
      line-height: 32px;
    }
  }

  &__list {
    counter-reset: steps;
    max-width: 758px;
    margin: 0 auto;
    justify-content: space-between;
    @include small-tablet {
      flex-wrap: wrap;
      gap: var(--offset);
    }
  }

  &__item {
    display: flex;
    align-items: center;
    flex-direction: column;

    &::before {
      counter-increment: steps;
      content: counter(steps);
      display: flex;
      justify-content: center;
      align-items: center;
      color: var(--light-color);
      border-radius: 100%;
      width: 48px;
      height: 48px;
      text-align: center;
      font-size: 16px;
      font-style: normal;
      line-height: 30px;
      flex-shrink: 0;
      background-color: #014C48;
      margin-bottom: 20px;
    }

    &--line {
      position: relative;
      flex-grow: 1;
      align-items: center;

      &::before {
        display: none;
      }

      &::after {
        content: '';
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        margin-top: -30px;
        width: 100%;
        height: 1px;
        flex-shrink: 0;
        background: rgba(53, 83, 98, 0.20);
      }
    }
    @include small-tablet {
      width: 100%;
      &::after {
        display: none;
      }
    }
  }

  &__descr {
    margin: 0;
    max-width: 142px;
    color: rgba(53, 83, 98, 0.80);
    text-align: center;
    font-size: 15px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px;
    /* 160% */
  }

}
