.hero {
  // display: flex;
  // flex-direction: column;
  background: url('../img/hero_bg.jpg') center center/cover no-repeat;
  min-height: 776px;
  padding-top: 190px;
  padding-bottom: 40px;
  color: var(--light-color);

  &__content {
    grid-column: 6 span;
    margin-bottom: 122px;
    @include small-tablet {
      grid-column: 4 span;
    }
  }

  &__title {
    margin: 0 0 29px 0;
    font-family: var(--second-family);
    font-size: 38px;
    font-style: normal;
    font-weight: 400;
    line-height: 44px;
    letter-spacing: 1.2px;
    @include tablet {
      font-size: 30px;
      line-height: 40px;
    }
    @include small-tablet {
      padding-right: 200px;
    }
    @include mobile {
      padding-right: 0px;
    }

  }

  &__descr {
    margin: 0 0 29px 0;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 28px;
    @include small-tablet {
      padding-right: 200px;
    }
    @include mobile {
      padding-right: 0px;
    }
  }


  &__subtitle {
    margin: 0;
    margin-bottom: 15px;
    font-size: var(--second-size);
    line-height: 24px;
    padding-top: 10px;
  }

  &__list {
    @include small-tablet {
      row-gap: var(--offset);
    }
  }


  &__item {
    grid-column: 2 span;
  }

  &__link {
    display: block;
    padding-top: 17px;
    border-top: 1px solid rgba(255, 255, 255, 0.60);
    font-size: var(--second-size);
    line-height: 24px;
    color: inherit;
    transition: color 0.3s ease-in-out, border-color 0.3s ease-in-out;
    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--accent-color);
      border-color: var(--accent-color);
    }

    &:active {
      color: var(--active-color);
      border-color: var(--active-color);
    }
  }
  .hero__link.focus-visible {
    outline: 1px solid var(--active-color);
  }
}
