.info {
  position: relative;
  display: flex;
  height: 680px;
  background-color: #001414;
  color: var(--light-color);

  &::before {
    content: '';
    width: 287px ;
    height: 100%;
    // background: url('../img/info/left_2x.png') center center/cover no-repeat;
    background-image: url('../img/info/left_2x.png') ;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    @include tablet {
      display: none;
    }
  }

  &::after {
    content: '';
    width: 287px ;
    height: 100%;
    // background: url('../img/info/right_2x.png') center center/cover no-repeat;
    background-image: url('../img/info/right_2x.png') ;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    @include tablet {
      display: none;
    }
  }

  &__content {
    display: flex;
    flex-grow: 1;
    justify-content: center;
    flex-direction: column;
    padding: 0 82px ;
    border-left: 1px solid var(--dark-color);
    border-right: 1px solid var(--dark-color);
    @include tablet {
      padding: var(--offset);
    }
  }
  &__text {
    width: 542px;
    margin: 0 auto;
    @include small-tablet {
      width: 320px;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 21px;
    font-weight: 400;
    color: inherit;
    font-size: 22px;
    line-height: 22px;
  }

  &__descr {
    font-weight: 400;
    margin: 0;
    color: inherit;
    font-size: var(--second-size);
    line-height: 24px;
  }
  @include tablet {
    height: auto;
  }
}
